<template>
    <div class="window">
        <div class="head">
            <td v-for="color in colors" :key="color">
                <div class="dots"  :style="color"/>
            </td>
            <td><div class="bar"/></td>
        </div>

        <div class="content">
            <el-row v-for="line in cata" :key="line">
                <icon v-if="line.icon === 'default_file'"><default_file/></icon>
                <icon v-if="line.icon === 'default_folder'"><default_folder/></icon>
                <icon v-if="line.icon === 'file_type_excel'"><file_type_excel/></icon>
                <icon v-if="line.icon === 'file_type_excel2'"><file_type_excel2/></icon>
                <icon v-if="line.icon === 'file_type_image'"><file_type_image/></icon>
                <icon v-if="line.icon === 'file_type_pdf2'"><file_type_pdf2/></icon>
                <icon v-if="line.icon === 'file_type_powerpoint'"><file_type_powerpoint/></icon>
                <icon v-if="line.icon === 'file_type_word'"><file_type_word/></icon>
                <a class="filename" :href="line.link" target="view_window">
                    {{ line.text }}
                </a>

            </el-row>
        </div>
    </div>
</template>
<script>

import icon from "./icon.vue"
import default_file from "./icons/default_file.vue"
import default_folder from "./icons/default_folder.vue"
import file_type_excel from "./icons/file_type_excel.vue"
import file_type_excel2 from "./icons/file_type_excel2.vue"
import file_type_image from "./icons/file_type_image.vue"
import file_type_pdf2 from "./icons/file_type_pdf2.vue"
import file_type_powerpoint from "./icons/file_type_powerpoint.vue"
import file_type_word from "./icons/file_type_word.vue"

export default {
    name: "window",
    components: {
        icon,
        default_file,
        default_folder,
        file_type_excel,
        file_type_excel2,
        file_type_image,
        file_type_pdf2,
        file_type_powerpoint,
        file_type_word
    },

    data() {
        return {
            colors: ["background-color: rgba(172, 65, 66, 0.95);", "background-color: rgba(232, 133, 59,0.95);", "background-color: rgba(148, 198, 107, 0.95);"],

        }
    },
    props: {
        cata: {
            type: Array,
            required: true
        }
    }
}
</script>
<style>
.el-row {
    vertical-align: middle;
    margin-bottom: 0;
}
  .window {
    width: 38vw;
    height: auto;
    margin-bottom: 3rem;
    border-radius: 0.8rem 0.8rem 0 0;
    border-width: 0.1rem;
    border-style: solid ;
    border-color: rgba(60,60,60,0.9);
    background-color: rgba(20,20,20,0.8);
  }

  .window .head {
    margin: 0.4vw 1vw 1vw 1vw;
  }

  .window .dots {
    width: 0.4vw;
    height: 0.4vw;
    border-radius: 50%;
    margin-left: 0.2vw;
  }

  .window .bar {
    margin-left: 9.8vw;
    width: 14vw;
    height: 0.6vw;
    border-radius: 5em / 5em;
    background-color: rgba(45,45,45,0.35);
  }

  .window .content {
      margin: 1rem 0 7rem 3vw;
      vertical-align: middle;
      text-align: left;
  }

.filename {
    color: white;
    text-decoration: none;
    margin-left: 1vw;
    font-size: 1.3vw;
    text-align: left;
    display: flex;
    justify-content: center;
    flex-direction: column;
    font-weight: 400;
}
</style>